import { styled } from '@mui/material/styles';
import Slider from '@mui/material/Slider';

export const CustInp = styled('input')`
border: 2px solid #828282;
border-radius: 100px;
font-family: 'Nunito Sans';
font-style: normal;
font-weight: 400;
font-size: 18px;
line-height: 24px;
background: none;
padding: 11.5px 19px;
&::placeholder{
   color: #2D2F36;
}
`
export const AirbnbSlider = styled(Slider)(() => ({
   color: '#828282',
   height: 3,
   padding: '15px 0px 0px !important',
   marginBottom: '79px',
   '& .MuiSlider-thumb': {
      height: 20,
      width: 20,
      backgroundColor: '#E5218F',
      '& .airbnb-bar': {
         height: 9,
         width: 1,
         marginLeft: 0,
         marginRight: 0,
      },
   },
   '& .MuiSlider-track': {
      height: 3,
   },
   '& .css-nnid7-MuiSlider-valueLabel': {
      background: 'none',
      color: '#2D2F36',
   },
}));